import $ from "jquery"

function xuLiDuLieuDate(date){
    if (date === ""){
        $(".errorNgay").text("Ngy khng c b trng")
        return false
    }

    $(".errorNgay").text("")
    return true
}
// errorMoTa

function xuLiDuLieuTien(soTien){
    if (soTien === ""){
        $(".errorTien").text("Tin khng c b trng")
        return false
    }
    if (soTien <= 999){
        $(".errorTien").text("Tin phi t 1.000 tr ln")
        return false
    }

    $(".errorTien").text("")
    return true
}

function xuLiDuLieuMoTa(moTa){
    if (moTa === ""){
        $(".errorMoTa").text("M t khng c b trng")
        return false
    }

    $(".errorMoTa").text("")
    return true
}

function kiemTraDuLieuHopLe(data) {
    if (xuLiDuLieuDate(data.date) && xuLiDuLieuTien(data.soTien) && xuLiDuLieuMoTa(data.moTa)){
        return true
    }
    return false
}

export { kiemTraDuLieuHopLe, xuLiDuLieuDate, xuLiDuLieuTien, xuLiDuLieuMoTa }